"user client";
import Header from "@/app/component/Header";
import { useEffect, useState } from "react";
import AxiosInstance from "@/app/component/AxiosInstance";

export default function Home() {
    const [themes, setThemes] = useState<[number, string][]>([]);

    useEffect(() => {
        AxiosInstance.post('/getTheme').then(
            (response) => {
                console.log(response);
                const themeData = response.data.map((theme: { id: number, name: string }) => [theme.id, theme.name]);
                setThemes(themeData);
            }
        ).catch((error) => {
            console.log(error);
        });
    }, []);

    return (
        <div>
            <Header />
            <div className="mainContent">
                <h2>Rudolf Steiner</h2>
                <div className="underHeaderTheme">
                    <img className="imgSteiner" src="https://upload.wikimedia.org/wikipedia/commons/thumb/f/f5/Steiner_um_1905.jpg/640px-Steiner_um_1905.jpg" alt="Rudolf Steiner" />
                </div>
                <div className="themeContainer">
                    {themes.map((theme) => (
                        <div className="themebox" key={theme[0]}>
                            <div>{theme[1]}</div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}